import None from '../../public/noNumber.vue'
  export default {
      name: "records",
    data(){
        return{
          api:this.GLOBAL.baseURL,
          beginMonth:'',
          beginYear:'',
          overMonth:'',
          overYear:'',
          monthList:[
            {label:'一月',value:'-01-01'},
            {label:'二月',value:'-02-01'},
            {label:'三月',value:'-03-01'},
            {label:'四月',value:'-04-01'},
            {label:'五月',value:'-05-01'},
            {label:'六月',value:'-06-01'},
            {label:'七月',value:'-07-01'},
            {label:'八月',value:'-08-01'},
            {label:'九月',value:'-09-01'},
            {label:'十月',value:'-10-01'},
            {label:'十一月',value:'-11-01'},
            {label:'十二月',value:'-12-01'},
          ],
          yearList:[],
          btnIndex:'',
          btnList:[
            {name:'全部'},
            {name:'收入'},
            {name:'支出'},
          ],
          recordList:[
          ],
          firstYear:'',
          total:0,
          index:1
        }
    },
    components:{
      None,
    },
    watch:{
      beginMonth:function(){

        this.getMoney()
      },
      beginYear:function(){

        this.getMoney()
      },
      overMonth: function(){

        this.getMoney()
      },
      overYear:function(){

        this.getMoney()
      }
    },
    methods:{
        getMoney(){
          if(this.beginMonth && this.beginYear && this.overMonth && this.overYear){
            this.index = 1
            this.getOtherCord()
          }
        },
      handleClick(tab, event) {
      },
      changeList(item,index){
          this.index = 1
        if(this.btnIndex != index && index != 0){
          this.btnIndex = index
          this.getOtherCord()
        }else if(this.btnIndex != index && index == 0){
          this.btnIndex = index
          this.getRecord()
        }
      },
      getOtherCord(){
         this.$http.post(this.api + '/TradCenter',{
          token: localStorage.getItem('token'),
          begin_time:this.beginYear +this.beginMonth,
          end_time:this.overYear + this.overMonth,
          page:this.index,
          type: this.btnIndex
        })
          .then(res=>{
            if(res.data.code == 1){
              this.recordList = res.data.data.data,
              this.total = res.data.data.total
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOtherCord(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      getRecord(val){
        this.$http.post(this.api + '/TradCenter',{
          token: localStorage.getItem('token'),
          begin_time:this.beginYear +this.beginMonth,
          end_time:this.overYear + this.overMonth,
          page:this.index
        })
          .then(res=>{
            if(res.data.code == 1){
              this.recordList = res.data.data.data,
              this.total = res.data.data.total
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getRecord()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      handleCurrentChange(val){
        this.index = val
          if(this.btnIndex){
            this.getOtherCord()
          }else{
            this.getRecord()
          }
      },
      getUser(){
        this.$http.post(this.api + '/home/User/get_user',{
          token: localStorage.getItem('token')
        })
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data[0]
              this.firstYear = data.create_time.split('-')[0]
              var date = new Date()
              let a = date.getFullYear() - parseInt(this.firstYear)
              for(let i = 0;i<=a;i++){
                let b = parseInt(this.firstYear) + i
                this.yearList.push({label:b,value:b})
              }
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getUser()
                })
            }else{
              alert(res.data.msg)
            }
          })
      }
    },
    mounted(){
      this.getUser()
      this.getRecord()
    }
  }



